import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
const mapStateToProps = (state) => {
  return state.User;
};

function AdminDashboard(props) {
  let display = "";

  if (props.dashboard) {
    const { dashboard } = props;
    const key = Object.keys(dashboard);
    console.log(key);
    display = key.map((element) => {
      let link = "";
      if (element === "Registered Student") link = "/admin/pendingVerification";
      if (element === "Applied Document") link = "/admin/pendingDocument";
      return (
        <div
          key={element}
          className="card col-lg-2 m-2 mx-auto"
          style={{ width: "80%" }}
        >
          <div className="card-header">
            <p style={{ margin: "auto" }}>{element}</p>
          </div>
          <p className="p-2">
            You have pennding {dashboard[element]} {element}
          </p>
          <Link to={link} className="btn btn-primary">
            Approve Now
          </Link>
        </div>
      );
    });
  }

  return (
    <div className="container mt-5">
      <div className="row text-center">{display}</div>
    </div>
  );
}

export default connect(mapStateToProps)(AdminDashboard);
